import React, { useState } from 'react';
import { Button, FormGroup, Row } from 'reactstrap';
import { connect } from 'react-redux';
import IntlMessages from '../../../../helpers/IntlMessages';
import {
  Colxx,
  Separator,
} from '../../../../components/common/CustomBootstrap';
import Breadcrumb from '../../../../containers/navs/Breadcrumb';
import { adminRoot } from '../../../../constants/defaultValues';
import UpdateContact from './UpdateContact';
import UpdateEmergencyContact from './UpdateEmergencyContact';

const Contact = ({ match }) => {
  const [editContact, toggleEditContact] = useState(false);
  const [editEmergency, toggleEditEmergency] = useState(false);

  return (
    <>
      <Row>
        <Colxx xxs="12">
          <Breadcrumb heading="menu.contact" match={match} />
          <Separator className="mb-5" />
        </Colxx>
      </Row>
      <Row>
        <Colxx xxs="12" md="6">
          <UpdateContact
            editContact={editContact}
            toggleEditContact={toggleEditContact}
          />
        </Colxx>

        <Colxx xxs="12" md="6">
          <UpdateEmergencyContact
            editEmergency={editEmergency}
            toggleEditEmergency={toggleEditEmergency}
          />
        </Colxx>
      </Row>

      <Row>
        <Colxx>
          <FormGroup className="text-center">
            <Button
              color="primary"
              className="m-3"
              disabled={editContact || editContact}
              href={`${adminRoot}/profile/links`}
            >
              <IntlMessages id="forms.previous" />
            </Button>
          </FormGroup>
        </Colxx>
      </Row>
    </>
  );
};

const mapStateToProps = ({ authUser }) => {
  const { currentUser } = authUser;
  return { currentUser };
};

const mapActionsToProps = {};

export default connect(mapStateToProps, mapActionsToProps)(Contact);
