import React from 'react';
import {
  Badge,
  Button,
  Card,
  CardBody,
  CardTitle,
  FormGroup,
  Input,
  Label,
} from 'reactstrap';
import {
  Colxx,
  Separator,
} from '../../../../components/common/CustomBootstrap';
import StateButton from '../../../../components/StateButton';
import IntlMessages from '../../../../helpers/IntlMessages';

const handleSuccessButtonClick = () => {
  return new Promise((success) => {
    setTimeout(() => {
      success('Everything went right!');
    }, 1400);
  });
};

const UpdateContact = ({ editContact, toggleEditContact }) => (
  <Card className="mb-4">
    <div className="position-absolute card-top-buttons">
      <Button
        outline
        color="primary"
        onClick={() => {
          toggleEditContact(!editContact);
        }}
        className="icon-button"
      >
        <i className="simple-icon-pencil" />
      </Button>
    </div>
    <CardBody>
      <CardTitle>
        <h2 className="lead">
          <IntlMessages id="user.update-contact" />
        </h2>
        <p>
          <IntlMessages id="user.update-contact-desc" />
        </p>
      </CardTitle>
      <FormGroup className="mb-3">
        <Label>
          <IntlMessages id="forms.email" />
        </Label>
        <Input
          type="text"
          name="email"
          value="abcd@example.com"
          disabled={!editContact}
        />
      </FormGroup>

      {editContact ? (
        <Badge color="danger" pill className="mt-3 mr-3 mb-3 p-2">
          <IntlMessages id="forms.email-not-verified" />
        </Badge>
      ) : (
        <Badge color="success" pill className="mt-3 mr-3 mb-3 p-2">
          <IntlMessages id="forms.email-verified" />
        </Badge>
      )}

      <Separator className="mb-4" />

      <FormGroup className="mb-3">
        <Label>
          <IntlMessages id="forms.phone" />
        </Label>
        <Input type="text" name="name" value="test" disabled={!editContact} />
      </FormGroup>

      <FormGroup className="mb-3">
        <Label>
          <IntlMessages id="forms.city" />
        </Label>
        <Input type="text" name="city" value="Delhi" disabled={!editContact} />
      </FormGroup>

      {editContact && (
        <Colxx xxs="12" className="text-center">
          <StateButton
            id="emergencySubmitButton"
            color="primary"
            onClick={handleSuccessButtonClick}
          >
            <IntlMessages id="forms.submit" />
          </StateButton>
        </Colxx>
      )}
    </CardBody>
  </Card>
);
export default UpdateContact;
