import React from 'react';
import {
  Button,
  Card,
  CardBody,
  CardTitle,
  FormGroup,
  Input,
  Label,
} from 'reactstrap';
import { Colxx } from '../../../../components/common/CustomBootstrap';
import StateButton from '../../../../components/StateButton';
import IntlMessages from '../../../../helpers/IntlMessages';

const handleSuccessButtonClick = () => {
  return new Promise((success) => {
    setTimeout(() => {
      success('Everything went right!');
    }, 1400);
  });
};

const UpdateEmergencyContact = ({ editEmergency, toggleEditEmergency }) => (
  <Card className="mb-4">
    <div className="position-absolute card-top-buttons">
      <Button
        outline
        color="primary"
        onClick={() => {
          toggleEditEmergency(!editEmergency);
        }}
        className="icon-button"
      >
        <i className="simple-icon-pencil" />
      </Button>
    </div>
    <CardBody>
      <CardTitle>
        <h2 className="lead">
          <IntlMessages id="user.update-emergeny-contact" />
        </h2>
        <p>
          <IntlMessages id="user.update-emergeny-contact-desc" />
        </p>
      </CardTitle>
      <FormGroup className="mb-3">
        <Label>
          <IntlMessages id="forms.emergency-contact-name" />
        </Label>
        <Input
          type="text"
          name="emergency-contact-name"
          value="Ramesh Rajpal"
          disabled={!editEmergency}
        />
      </FormGroup>
      <FormGroup className="mb-3">
        <Label>
          <IntlMessages id="forms.emergency-contact-phone" />
        </Label>
        <Input
          type="text"
          name="emergency-contact-phone"
          value="1223334444"
          disabled={!editEmergency}
        />
      </FormGroup>

      {editEmergency && (
        <Colxx xxs="12" className="text-center">
          <StateButton
            id="emergencySubmitButton"
            color="primary"
            onClick={handleSuccessButtonClick}
          >
            <IntlMessages id="forms.submit" />
          </StateButton>
        </Colxx>
      )}
    </CardBody>
  </Card>
);

export default UpdateEmergencyContact;
